.popup__content {
    box-sizing: border-box;
    position: relative;
    width: calc((100vw - 320px) / (1280 - 320) * (430 - 282) + 282px);
    padding: calc((100vw - 320px) / (1280 - 320) * (34 - 25) + 25px) calc((100vw - 320px) / (1280 - 320) * (36 - 22) + 22px) calc((100vw - 320px) / (1280 - 320) * (37 - 25) + 25px);
    background-color: #fff;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
    border-radius: 10px;
}

@media screen and (min-width: 1280px) {
    .popup__content {
        padding: 34px 36px 37px;
        width: 430px;
    }
}
