.popup__close-button {
    position: absolute;
    top: -40px;
    right: -40px;
    width: calc((100vw - 320px) / (1280 - 320) * (32 - 20) + 20px);
    height: calc((100vw - 320px) / (1280 - 320) * (32 - 20) + 20px);
    outline: none;
    border: none;
    background: transparent url(../../../images/popup/Close.svg) center center / cover no-repeat;
    transition: all 0.2s linear;
}

.popup__close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (min-width: 1280px) {
    .popup__close-button {
        width: 32px;
        height: 32px;
        background-size: 32px;
    }
}

@media screen and (max-width: 767px) {
    .popup__close-button {
        right: 0px;
        top: -36px;
    }
}

